import React from "react";
import "./Navbar.css";
import { Link, useLocation } from "react-router-dom";
import { IconRoutesPages } from "./IconRoutesPages";

const Navbar = () => {
  const { pathname } = useLocation();
  return (
    <nav
      className="styleNavbar d-flex align-items-end justify-content-md-center justify-content-start ps-1 ps-md-auto text-white w-100"
      style={{
        height: "3rem",
        backgroundColor: "var(--primary)",
        overflowX: "auto",
      }}
    >
      <ul className="d-flex w-100 justify-content-around  align-items-center mb-0 ps-0 w-100 fs-3">
        {IconRoutesPages.map(({ icon, to }) => (
          <>
            {/* <b className={`${pathname === to && "bg-white "}`} /> */}
            <Link
              to={to}
              className={`${
                pathname === to ? " text-black  bg-white " : " text-red-color"
              }`}
            >
              {icon}
            </Link>
            {/* <c className={`${pathname === to && "bg-white"}`} /> */}
          </>
        ))}
      </ul>
    </nav>
  );
};
export default Navbar;
